<template>
  <a-spin :spinning="loading">
    <a-form-model
      ref="form"
      :model="entity"
      :rules="rules"
      autocomplete="off"
      layout="vertical"
    >
      <div class="card">
        <a-row :gutter="24">
          <a-col :sm="6">
            <!-- 生产线编码 -->
            <a-form-model-item
              label="生产线编码"
              prop="lineCode"
              ref="lineCode"
            >
              <a-input
                v-model="entity.lineCode"
                placeholder="请输入生产线编码"
                name="lineCode"
                :disabled="!!ndoEditParams.row"
                @change="inChange"
              ></a-input>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 生产线名称 -->
            <a-form-model-item
              label="生产线名称"
              prop="name"
              ref="name"
            >
              <a-input
                v-model="entity.name"
                placeholder="请输入生产线名称"
                name="name"
                @change="inChange"
              ></a-input>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 生产线类型 -->
            <a-form-model-item
              label="生产线类型"
              prop="lineType"
              ref="lineType"
            >
              <a-select
                v-model="entity.lineType"
                placeholder="请选择生产线类型"
                name="lineType"
              >
                <a-select-option value="装配线">装配线</a-select-option>
                <a-select-option value="加工线">加工线</a-select-option>
                <a-select-option value="包装线">包装线</a-select-option>
                <a-select-option value="检测线">检测线</a-select-option>
                <a-select-option value="喷涂线">喷涂线</a-select-option>
              </a-select>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 所属工厂 -->
            <a-form-model-item
              label="所属工厂"
              prop="factoryId"
              ref="factoryId"
            >
              <a-select
                v-model="entity.factoryId"
                placeholder="请选择所属工厂"
                name="factoryId"
                @change="handleFactoryChange"
              >
                <a-select-option value="1">生产工厂1</a-select-option>
                <a-select-option value="2">组装工厂2</a-select-option>
                <a-select-option value="3">加工工厂3</a-select-option>
                <a-select-option value="4">储存中心4</a-select-option>
                <a-select-option value="5">物流中心5</a-select-option>
              </a-select>
            </a-form-model-item>
          </a-col>
        </a-row>
        <a-row :gutter="24">
          <a-col :sm="6">
            <!-- 日产能 -->
            <a-form-model-item
              label="日产能"
              prop="capacity"
              ref="capacity"
            >
              <a-input
                v-model="entity.capacity"
                placeholder="请输入日产能"
                name="capacity"
                @change="inChange"
              ></a-input>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 建立时间 -->
            <a-form-model-item
              label="建立时间"
              prop="foundDate"
              ref="foundDate"
            >
              <a-date-picker
                v-model="entity.foundDate"
                style="width: 100%"
                name="foundDate"
                @change="inChange"
              />
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 生产线长度 -->
            <a-form-model-item
              label="生产线长度"
              prop="length"
              ref="length"
            >
              <a-input-number
                v-model="entity.length"
                style="width: 100%"
                :min="0"
                :precision="2"
                name="length"
                @change="inChange"
              />
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 生产线宽度 -->
            <a-form-model-item
              label="生产线宽度"
              prop="width"
              ref="width"
            >
              <a-input-number
                v-model="entity.width"
                style="width: 100%"
                :min="0"
                :precision="2"
                name="width"
                @change="inChange"
              />
            </a-form-model-item>
          </a-col>
        </a-row>
        <a-row :gutter="24">
          <a-col :sm="6">
            <!-- 人员数量 -->
            <a-form-model-item
              label="人员数量"
              prop="staffCount"
              ref="staffCount"
            >
              <a-input-number
                v-model="entity.staffCount"
                style="width: 100%"
                :min="0"
                name="staffCount"
                @change="inChange"
              />
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 运行状态 -->
            <a-form-model-item
              label="运行状态"
              prop="status"
              ref="status"
            >
              <a-select
                v-model="entity.status"
                placeholder="请选择运行状态"
                name="status"
              >
                <a-select-option value="运行中">运行中</a-select-option>
                <a-select-option value="维护中">维护中</a-select-option>
                <a-select-option value="停产">停产</a-select-option>
                <a-select-option value="调试中">调试中</a-select-option>
              </a-select>
            </a-form-model-item>
          </a-col>
          <a-col :sm="12">
            <!-- 描述 -->
            <a-form-model-item
              label="描述"
              prop="description"
              ref="description"
            >
              <a-textarea
                v-model="entity.description"
                placeholder="请输入描述信息"
                :rows="4"
                :max-length="1000"
                name="description"
                @change="inChange"
              />
            </a-form-model-item>
          </a-col>
        </a-row>
      </div>

      <!-- 设备信息 -->
      <g-collapse headerName="设备信息">
        <template #content>
          <a-row :gutter="24">
            <a-col :span="24">
              <div class="table-operations">
                <a-button type="primary" @click="addEquipment">
                  <a-icon type="plus" />添加设备
                </a-button>
              </div>
              <a-table
                :columns="equipmentColumns"
                :data-source="entity.equipmentList"
                :pagination="false"
                rowKey="id"
              >
                <template #action="{ record }">
                  <a-space>
                    <a @click="editEquipment(record)">编辑</a>
                    <a-divider type="vertical" />
                    <a-popconfirm
                      title="确定要删除该设备吗？"
                      @confirm="deleteEquipment(record)"
                    >
                      <a>删除</a>
                    </a-popconfirm>
                  </a-space>
                </template>
              </a-table>
            </a-col>
          </a-row>
        </template>
      </g-collapse>

      <!-- 人员信息 -->
      <g-collapse headerName="人员信息">
        <template #content>
          <a-row :gutter="24">
            <a-col :span="24">
              <div class="table-operations">
                <a-button type="primary" @click="addStaff">
                  <a-icon type="plus" />添加人员
                </a-button>
              </div>
              <a-table
                :columns="staffColumns"
                :data-source="entity.staffList"
                :pagination="false"
                rowKey="id"
              >
                <template #action="{ record }">
                  <a-space>
                    <a @click="editStaff(record)">编辑</a>
                    <a-divider type="vertical" />
                    <a-popconfirm
                      title="确定要删除该人员吗？"
                      @confirm="deleteStaff(record)"
                    >
                      <a>删除</a>
                    </a-popconfirm>
                  </a-space>
                </template>
              </a-table>
            </a-col>
          </a-row>
        </template>
      </g-collapse>

      <!-- 元数据信息 -->
      <g-collapse headerName="元数据信息">
        <template #content>
          <a-row :gutter="16">
            <a-col :span="12">
              <a-form-model-item label="创建人">
                <a-input v-model="entity.createdBy" disabled />
              </a-form-model-item>
            </a-col>
            <a-col :span="12">
              <a-form-model-item label="创建时间">
                <a-input v-model="entity.createdTime" disabled />
              </a-form-model-item>
            </a-col>
          </a-row>
          <a-row :gutter="16">
            <a-col :span="12">
              <a-form-model-item label="修改人">
                <a-input v-model="entity.modifiedBy" disabled />
              </a-form-model-item>
            </a-col>
            <a-col :span="12">
              <a-form-model-item label="修改时间">
                <a-input v-model="entity.modifiedTime" disabled />
              </a-form-model-item>
            </a-col>
          </a-row>
        </template>
      </g-collapse>
    </a-form-model>
  </a-spin>
</template>

<script>
import { GNdoPageEditBase } from "@/utils";

export default {
  name: "CreateOrEditMfgLine",
  mixins: [GNdoPageEditBase],
  data() {
    return {
      entity: {
        lineCode: "",
        name: "",
        lineType: "",
        factoryId: undefined,
        factoryName: "",
        capacity: "",
        foundDate: null,
        length: 0,
        width: 0,
        staffCount: 0,
        description: "",
        status: "",
        equipmentList: [],
        staffList: [],
        createdBy: "",
        createdTime: "",
        modifiedBy: "",
        modifiedTime: ""
      },
      loading: false,
      rules: {
        lineCode: [
          { required: true, message: "请输入生产线编码", trigger: "blur" },
          { pattern: /^ML\d{4}$/, message: "生产线编码必须为ML+4位数字", trigger: "blur" }
        ],
        name: [
          { required: true, message: "请输入生产线名称", trigger: "blur" }
        ],
        lineType: [
          { required: true, message: "请选择生产线类型", trigger: "change" }
        ],
        factoryId: [
          { required: true, message: "请选择所属工厂", trigger: "change" }
        ],
        capacity: [
          { required: true, message: "请输入日产能", trigger: "blur" }
        ],
        foundDate: [
          { required: true, message: "请选择建立时间", trigger: "change" }
        ],
        length: [
          { required: true, message: "请输入生产线长度", trigger: "blur" }
        ],
        width: [
          { required: true, message: "请输入生产线宽度", trigger: "blur" }
        ],
        staffCount: [
          { required: true, message: "请输入人员数量", trigger: "blur" }
        ],
        status: [
          { required: true, message: "请选择运行状态", trigger: "change" }
        ]
      },
      equipmentColumns: [
        {
          title: "设备编码",
          dataIndex: "equipmentCode",
          key: "equipmentCode",
        },
        {
          title: "设备名称",
          dataIndex: "equipmentName",
          key: "equipmentName",
        },
        {
          title: "设备类型",
          dataIndex: "equipmentType",
          key: "equipmentType",
        },
        {
          title: "设备状态",
          dataIndex: "equipmentStatus",
          key: "equipmentStatus",
        },
        {
          title: "操作",
          key: "action",
          scopedSlots: { customRender: "action" },
        },
      ],
      staffColumns: [
        {
          title: "工号",
          dataIndex: "staffCode",
          key: "staffCode",
        },
        {
          title: "姓名",
          dataIndex: "staffName",
          key: "staffName",
        },
        {
          title: "岗位",
          dataIndex: "position",
          key: "position",
        },
        {
          title: "技能等级",
          dataIndex: "skillLevel",
          key: "skillLevel",
        },
        {
          title: "操作",
          key: "action",
          scopedSlots: { customRender: "action" },
        },
      ],
    };
  },

  mounted() {},

  methods: {
    // 初始化
    init() {
      console.log(this.id, "初始化");
      this.loading = true;
      if (this.id) {
        // 模拟请求获取生产线数据
        setTimeout(() => {
          this.loading = false;
          this.entity = {
            lineCode: "ML0001",
            name: "装配线1",
            lineType: "装配线",
            factoryId: "1",
            factoryName: "生产工厂1",
            capacity: "1000件/天",
            foundDate: "2024-01-01",
            length: 100,
            width: 5,
            staffCount: 20,
            description: "用于产品装配的生产线",
            status: "运行中",
            equipmentList: [
              {
                id: 1,
                equipmentCode: "EQ001",
                equipmentName: "装配机1",
                equipmentType: "装配设备",
                equipmentStatus: "运行中"
              }
            ],
            staffList: [
              {
                id: 1,
                staffCode: "ST001",
                staffName: "张三",
                position: "装配工",
                skillLevel: "高级"
              }
            ],
            createdBy: "admin",
            createdTime: "2024-03-19 10:00",
            modifiedBy: "admin",
            modifiedTime: "2024-03-19 14:00"
          };
        }, 500);
      } else {
        this.reset();
        this.loading = false;
      }
    },
    resetFormData() {
      // 重置表单绑定的数据源
      this.entity = {
        lineCode: "",
        name: "",
        lineType: "",
        factoryId: undefined,
        factoryName: "",
        capacity: "",
        foundDate: null,
        length: 0,
        width: 0,
        staffCount: 0,
        description: "",
        status: "",
        equipmentList: [],
        staffList: [],
        createdBy: "",
        createdTime: "",
        modifiedBy: "",
        modifiedTime: ""
      };
    },
    async save() {
      // 保存 校验
      let res = await this.checkError();
      if (res) {
        this.loading = true;
        setTimeout(() => {
          this.loading = false;
          this.saveSuccess(this.entity.name);
          this.handleSaveEndBackView(this.id);
          this.refreshParent({
            reset: null,
          });
        }, 500);
      }
    },
    inChange(e) {
      console.log(e.target.value);
    },
    handleFactoryChange(value) {
      const factoryNames = {
        "1": "生产工厂1",
        "2": "组装工厂2",
        "3": "加工工厂3",
        "4": "储存中心4",
        "5": "物流中心5"
      };
      this.entity.factoryName = factoryNames[value];
    },
    addEquipment() {
      // 添加设备
      this.entity.equipmentList.push({
        id: this.entity.equipmentList.length + 1,
        equipmentCode: "",
        equipmentName: "",
        equipmentType: "",
        equipmentStatus: ""
      });
    },
    editEquipment(record) {
      // 编辑设备
      console.log("编辑设备", record);
    },
    deleteEquipment(record) {
      // 删除设备
      const index = this.entity.equipmentList.findIndex(item => item.id === record.id);
      if (index > -1) {
        this.entity.equipmentList.splice(index, 1);
      }
    },
    addStaff() {
      // 添加人员
      this.entity.staffList.push({
        id: this.entity.staffList.length + 1,
        staffCode: "",
        staffName: "",
        position: "",
        skillLevel: ""
      });
    },
    editStaff(record) {
      // 编辑人员
      console.log("编辑人员", record);
    },
    deleteStaff(record) {
      // 删除人员
      const index = this.entity.staffList.findIndex(item => item.id === record.id);
      if (index > -1) {
        this.entity.staffList.splice(index, 1);
      }
    }
  },
};
</script>

<style lang="scss" scoped>
.table-operations {
  margin-bottom: 16px;
}
</style> 